.Panel::after {
	background-size: cover;
}
.Group>:not(.Header):first-child {
	color: #000;
}
body[scheme="space_gray"] .Panel::after {
	background-size: cover;
}
body[scheme="space_gray"] .Group>:not(.Header):first-child {
	color: #fff;
}
.Panel .Panel__in {
	background-color: transparent;
}
.Cell__description {
	color: #30ab65
}
@media screen and (max-width: 333px) {
	.Button--sz-l .Button__content {
		font-size: 13px;
	}
}
@media screen and (max-width: 313px) {
	.Button--sz-l .Button__content {
		font-size: 11px;
	}
}
@media screen and (max-width: 298px) {
	.Button--sz-l .Button__content {
		font-size: 9px;
	}
}
@media screen and (max-width: 283px) {
	.Button--sz-l .Button__content {
		font-size: 8.2px;
	}
}